import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getBrands,
  brandEdit,
  brandDelete,
  isModelVisible,
  SetBrandParams,
} from "../../../../store/Masters/Brand/brandSlice";
import AddBrand from "./addBrand";

function Brands(props) {
  useEffect(() => {
    props.getBrands({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Brand Name",
      dataIndex: "name",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterMode: "tree",
      filterSearch: true,
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_brand") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.brandEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_brand") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.brandDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
          )}
        </Space>
      ),
    },
  ];

  const data = props.brandslist.map((row, key) => ({
    ...row,
    index: (props.brandParams.page - 1) * props.brandParams.page_size + key + 1,
  }));

  function onPageChange(page, pageSize) {
    props.SetBrandParams({
      ...props.brandParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getBrands({
      ...props.brandParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetBrandParams({ ...props.brandParams, search: val });
    props.getBrands({ ...props.brandParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Brand Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_brand") !== -1 &&
          "Add Brand"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-16">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getBrands({
                      ...props.brandParams,
                      page: 1,
                      search: "",
                    });
                    props.SetBrandParams({
                      ...props.brandParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.brandParams?.page}
                pageSize={props.brandParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddBrand />
    </Row>
  );
}

function mapStateToProps({ brands, system }) {
  return {
    brandslist: brands.brandslist,
    noofpages: brands.noofpages,
    listCount: brands.listCount,
    brandParams: brands.brandParams,
    loading: brands.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getBrands,
  brandEdit,
  brandDelete,
  isModelVisible,
  SetBrandParams,
})(Brands);
